import React from "react";
import "./logo.css";
const OrangeTheme = require("../../assets/headphones.svg") as string;
const PurpleTheme = require("../../assets/headphones2.svg") as string;

export interface LogoProps {
  /**
   * Is this the principal call to action on the page?
   */
  primary?: boolean;
  /**
   * What background color to use
   */
  backgroundColor?: string;
  /**
   * How large should the button be?
   */
  size?: "small" | "medium" | "large";
  /**
   * What color should the logo be?
   */
  image?: "OrangeTheme" | "PurpleTheme";
  /**
   * Button contents
   */
  /**
   * Optional click handler
   */
  onClick?: () => void;
}

/**
 * Primary UI component for user interaction
 */
export const Logo: React.FC<LogoProps> = ({ image = "OrangeTheme" }) => {
  const mode = image === "OrangeTheme" ? PurpleTheme : OrangeTheme;
  return <img className="headphones-logo" src={mode} alt="I'm Listeninglogo" />;
};
